import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the ProductOverView page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-productOverView',
  templateUrl: 'productOverView.html',
})
export class ProductOverViewPage {

  userName: string;
  idioms: any[] = [];
  public message: string;
  private lang: any;
  public accountLst: string[];
  public creditLst: string[];
  public loansLst: string[] = null;
  public investmentLst: string[] = null;
  public usuario: string;
  public fecha: string;
  public commands: Array<string[]>;
  cuenta: any;
  public requestSecurity: string[]; //Variable del OTP
  public loanBoolean: boolean = false;
  public validacionOTP: number;

  public verAccount: boolean = false;
  public verCreditCard: boolean = false;
  public verLoan: boolean = false;
  public verInvestment: boolean = false;

  //obtencion del valided de security otp para no volver a pedir el OTP
  public valide: boolean;
  public procesoOTP: number=0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, private translateService: TranslateService, public http: HttpProvider, public utils: UtilsProvider,
    public loading: LoadingController, public command: CommandsProvider) {

    

  }
/*
  ionViewCanEnter() {

    //preguntar si el comando posee parametro en el utils para comparar si viene en la posicion 1
    //en caso de ser igual debera de redireccionar a pantalla de otp
    //tener una pantalla basica con OTP donde se solicite un campo de ingreso y boton de aceptar

    //Valor para solicitar el OTP
    this.requestSecurity = this.utils.getCommands(this.command.E2ADMR43);
    this.valide = this.navParams.get('valided');

     //Obtencion de la variable de sesion para no volver a pedir el OTP
     this.storage.get('procesoOTP').then((val) => {      
      if (val != null) {
        this.procesoOTP = val;           
      }
      this.navToOTP();
    });  
    
  }*/

  private navToOTP(){    
    //return confirm("deseas ingresar typee el otp");
    if (this.requestSecurity[1] == '*' && this.procesoOTP!=1 && this.valide!=true) {
      this.navCtrl.push("SecurityOtpPage",{cmdAux: 'E2ADMR43XXXX'}); 
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductOverView');
    this.lang = this.translateService.getDefaultLang();
    this.utils.getLanguage();
    this.utils.getSessionId();

    this.storage.get('accountLst').then((val) => { if (val != null && val != "") { this.accountLst = val; this.verAccount = true; } else { this.verAccount = false; } });
    this.storage.get('creditLst').then((val) => { if (val != null && val != "") { this.creditLst = val; this.verCreditCard = true; } else { this.verCreditCard = false; } });
    this.storage.get('loansLst').then((val) => { if (val != null && val != "") { this.loansLst = val; this.verLoan = true; } else { this.verLoan = false; } });
    this.storage.get('investmentLst').then((val) => { if (val != null && val != "") { this.investmentLst = val; this.verInvestment = true; } else { this.verInvestment = false; } });

    this.storage.get('usuario').then((val) => { if (val != null) { this.usuario = val; } });
    this.storage.get('fecha').then((val) => { if (val != null) { this.fecha = val; } });



  }

  //Metodo Loanding
  presentLoadingDefault() {
    let loading = this.loading.create({
      content: 'Please wait...'
    });

    loading.present();

    setTimeout(() => {
      loading.dismiss();
    }, 2000);
  }

  ionViewDidEnter() {

     
   

    if ((this.accountLst == null || this.accountLst == undefined) && (this.creditLst == null || this.accountLst == undefined) &&
      (this.loansLst == null || this.loansLst == undefined) && (this.investmentLst == null || this.investmentLst == undefined)) {

      this.loadProductOverView();
      //Carga del metodo Loanding
      //this.presentLoadingDefault();
    }
  }

  /* Metodos del ACCOUNT */
  //Boton Izquierda "mas"
  abrirCuenta(i, valor, numero, tipo) {
    //alert("valor i-> "+i+ "\n valor VALOR-> "+valor+"\n valor numero-> "+numero+"\n valor tipo-> "+tipo);
    this.navCtrl.push("DetalleCuentaPage", {
      valor: valor,
      //detAccountData: this.accountLst[i],
      detAccountData:  ( (this.accountLst != null && this.accountLst != [""] && this.accountLst != undefined) ? this.accountLst[i] : ""),
      //creditData: this.creditLst[i],
      creditData: ( (this.creditLst != null && this.creditLst != [""] && this.creditLst != undefined) ? this.creditLst[i] : ""),
      //loanData: this.loansLst[i], if (res.lista3 != null && res.lista3 != [""] && res.lista3 != "" && res.lista3 != undefined)
      loanData: ( (this.loansLst != null && this.loansLst != [""] && this.loansLst != undefined) ? this.loansLst[i] : ""),
      //investmentData: this.investmentLst[i]
      investmentData: ( (this.investmentLst != null && this.investmentLst != [""] && this.investmentLst != undefined) ? this.investmentLst[i] : "")
    });

    this.storage.set('noCuenta', numero);
    this.storage.set('tipoCuenta', tipo);

  }
  //Boton derecha "flecha"
  movtos(numero, tipo) {
    this.navCtrl.push("DetalleConsultaPage");
    this.storage.set('noCuenta', numero);
    this.storage.set('tipoCuenta', tipo);
  }

  //Metodos del CREDIT CARD
  creditCard(creditCardSelected) {
    this.navCtrl.push("CreditoMovimientoPage");
    
    //se guarda en sesion la tarjeta de credito que se selecciono para
    //el cuando se haga la consulta de movimiento por el filtro
    this.storage.set('creditCardSelected', creditCardSelected);
  }

  getData() {

    this.storage.get('user').then((val) => {
      this.userName = val;
    });
  }


  /**
   * @author
   * @argument void
   */
  private loadProductOverView() {

    //var command = 'E2SECR61P';

    if (this.utils.getCommands != null && this.utils.getCommands != undefined && this.utils.getCommands.length > 0) {
      var command: string[] = this.utils.getCommands(this.command.E2SECR61P);

      //var param = command + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+'&E2EW=null' + this.utils.separator + 'Prueba' + this.utils.separator + this.utils.separator + 'E2NCNR01' + this.utils.separator + 'null' + this.utils.separator + '%BA%20&PAR=' + this.utils.separator;
      var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID=' + this.utils.getSessionId() + '&E2EW=null' + this.utils.separator + 'Prueba' + this.utils.separator + this.utils.separator + 'E2NCNR01' + this.utils.separator + 'null' + this.utils.separator + '%BA%20&PAR=' + this.utils.separator;
      this.http.callServer(param).then(res => {
        this.message = res.message;

        if (this.message != '' && typeof this.message !== "undefined") {
          this.utils.alertServidor(this.message);
        } else {

          if (res.lista1 != null && res.lista1 != [""] && res.lista1 != "" && res.lista1 != undefined) {
            this.verAccount = true;
            this.accountLst = res.lista1;
          } else { this.verAccount = false; }

          if (res.lista2 != null && res.lista2 != [""] && res.lista2 != "" && res.lista2 != undefined) {
            this.verCreditCard = true;
            this.creditLst = res.lista2;
          } else { this.verCreditCard = false; }

          if (res.lista3 != null && res.lista3 != [""] && res.lista3 != "" && res.lista3 != undefined) {
            this.verLoan = true;
            this.loansLst = res.lista3;
          } else { this.verLoan = false; }

          if (res.lista4 != null && res.lista4 != [""] && res.lista4 != "" && res.lista4 != undefined) {
            this.verInvestment = true;
            this.investmentLst = res.lista4;
          } else { this.verInvestment = false; }

          //Se guarda en sesion todas las listas y asi no volver hacer peticiones la servidor
          this.storage.set("accountLst", res.lista1);
          this.storage.set("creditLst", res.lista2);
          this.storage.set("loansLst", res.lista3);
          this.storage.set("investmentLst", res.lista4);

        }
      },
        error => {
          console.log(error);
        });
    }
  } 

  logout() {
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }



}



